.topNav-wrapper {
  height: 69px;
  -webkit-box-shadow: 0 0 0 0 black;
          box-shadow: 0 0 0 0 black;
  line-height: 69px;
  position: fixed;
  top: 0;
  z-index: 10086;
  left: 0;
  width: 100%;
  background: #000000b8;
  color: inherit;
}
.topNav-wrapper .title {
  -webkit-animation: lightWeak 5s;
          animation: lightWeak 5s;
  color: #d0d0d0;
}
.topNav-wrapper .column.float-right {
  margin-right: 10px;
}
.topNav-wrapper h3 {
  line-height: inherit;
  margin-bottom: inherit;
}
.image-wrapper {
  position: fixed;
  z-index: 10010;
}
@-webkit-keyframes lightWeak {
  0% {
    color: #545454;
  }
  10% {
    color: #808080;
  }
  20% {
    color: #515151;
  }
  30% {
    color: #c6c4c4;
  }
  40% {
    color: #bababa;
  }
  50% {
    color: #686868;
  }
  100% {
    color: #d0d0d0;
  }
}
@keyframes lightWeak {
  0% {
    color: #545454;
  }
  10% {
    color: #808080;
  }
  20% {
    color: #515151;
  }
  30% {
    color: #c6c4c4;
  }
  40% {
    color: #bababa;
  }
  50% {
    color: #686868;
  }
  100% {
    color: #d0d0d0;
  }
}
.content-wrapper {
  position: fixed;
  top: 69px;
  left: 0;
  z-index: 10086;
  height: calc(100vh - 69px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
}
.content-wrapper .content-panel {
  height: 85%;
  width: 100%;
  background: #000000c9;
}
.content-wrapper .notice-text {
  position: absolute;
  right: 0;
  bottom: 0;
  color: white;
  opacity: 0.6;
  margin: 3px 8px;
}



